        height: 65vh;
    }

    .ProductSelectedBox.Light{
        background: #526174;
    }
    .ProductSelectedImageBox{
        flex: 1;

        display: flex;
        justify-content: center;
        align-items: center;

        /* background: black; */
    }

    .ProductSelectedImageBox p{
