<template>
    <div class="Products">
        <div class="LoadingBox" v-if="is_loading">
            <Loading size="10" :color2="`brown`" :text="shared.translate.TR_LOADING_MYADS" :use_background="false" :shared="shared" />
        </div>

        <div v-else>
        <div v-if="!page_product">
            <div class="ProductsHeaderBox">
                <div class="ProductsSearchBox">
                    <i class="fas fa-search"></i>
                    <input type="text" :placeholder="shared.translate.TR_SEARCH" v-model="search">
                </div>

                <div class="ProductsOrderByBox">
                    <p>{{shared.translate.TR_SORTBY}}:</p>
                    <p :class="{selected : sort_by == 0}" @click="SetSortBy(0)">{{shared.translate.TR_NAME}}</p>
                    <p :class="{selected : sort_by == 1}" @click="SetSortBy(1)">{{shared.translate.TR_LOWEST}}</p>
                    <p :class="{selected : sort_by == 2}" @click="SetSortBy(2)">{{shared.translate.TR_BIGGEST}}</p>
                </div>
            </div>

            <div class="ProductsListItemsBox">
                <div class="ProductsItemBox" v-for="(item, index) in SortProducts().filter(el => el.label.toLowerCase().includes(search.toLowerCase()))" :key="`myad:${index}`">
                    <div class="OnClick" @click="SelectProduct(item)"></div>
                    <p class="ProductsItemName">{{functions.ReduceString(item.label, 13)}} x{{item.amount}}</p>
                    <p class="ProductsItemPrice">{{shared.translate.TR_SIMBOL_MONEY}} {{item.price}}</p>
                    <div class="ProductsItemImageBox">
                        <img class="ProductsItemImage" :src="`img/${item.name}.png`" alt="">
                    </div>
                    <p class="ProductsItemOwner">{{shared.translate.TR_BY}} {{item.owner}}</p>
                    <div class="MyAdsItemButton">
                        <p>{{shared.translate.TR_REMOVE}}</p>
                    </div>
                </div>
            </div>
        </div>

        <div class="ProductSelectedBox" v-else>
            <div class="ButtonBack">
                <div class="OnClick" @click="page_product = false"></div>
                <i class="fas fa-arrow-left"></i>
                <p>{{shared.translate.TR_BACK}}</p>
            </div>
            <div class="ProductSelectedImageBox">
                <p>{{selected_product.label.toUpperCase()}}</p>
                <div>
                <img :src="`img/${selected_product.name}.png`" alt="">
                </div>
            </div>

            <div class="ProductSelectedInfosBox">
                <p class="ProductSelectedName">{{selected_product.label}}</p>
                <p class="ProductSelectedOwner">{{selected_product.owner}}</p>
                <div class="ProductSelectedDatasBox">
                    <div>
                        <p>{{shared.translate.TR_TOTAL_AMOUNT}}: </p>
                        <p>{{selected_product.amount}}</p>
                    </div>

                    <div v-if="selected_product.weight">
                        <p>{{shared.translate.TR_WEIGHT}}: </p>
                        <p>{{selected_product.weight}}</p>
                    </div>

                    <div>
                        <p>{{shared.translate.TR_PRICE_PRODUCT}}: </p>
                        <p>{{shared.translate.TR_SIMBOL_MONEY}} {{selected_product.price}}</p>
                    </div>

                    <div>
                        <p>{{shared.translate.TR_APPLICATION_FEE}} {{shared.application_fee*100}}%: </p>
                        <p>{{shared.translate.TR_SIMBOL_MONEY}} {{Math.floor(selected_product.price * shared.application_fee)}}</p>
                    </div>
                </div>

                <p class="ProductSelectedPrice">{{shared.translate.TR_SIMBOL_MONEY}} {{Math.floor(Number(selected_product.price) + Number(selected_product.price * shared.application_fee))}}</p>
                <p class="NewAdSelectedTextPriceFee">{{shared.translate.TR_MESSAGE_REMOVE}}</p>

                <div class="MyAdsSelectedButtonsBox">
                    <div class="MyAdsSelectedBuyButton">
                        <div class="OnClick" @click="RemoveItem"></div>
                        <i class="fas fa-times"></i>
                        <p>{{shared.translate.TR_CONFIRM_REMOVE}}</p>
                    </div>
                </div>
            </div>
        </div>
        </div>
        
    </div>
</template>

<script>

import Loading from '../components/Loading.vue';

export default {
    components: {
       Loading,
    },

    props:{
        shared: {},
        functions: {},
    },

    data: function(){
        return({
            products : [
                {name: "energy", label: "Energy", amount: 500, weight: 1, price: 1000, identifier: "asdad", owner: "Anonymous"},
                {name: "firstaid", label: "First Aid", amount: 500, weight: 1, price: 2000, identifier: "asdad", owner: "Anonymous"},
                {name: "weapon_pistol", label: "Pistol", amount: 500, weight: 1, price: 3000, identifier: "asdad", owner: "Anonymous"},
                {name: "weapon_heavyshotgun", label: "Heavy Shotgun", amount: 500, weight: 1, price: 5000, identifier: "asdad", owner: "Anonymous"},
                {name: "weapon_heavyshotgun", label: "Heavy Shotgun", amount: 500, weight: 1, price: 51000, identifier: "asdad", owner: "Anonymous"},
            ],

            search: "",
            sort_by: 0,

            selected_product: {},

            page_product: false,

            is_loading: true,
        })
    },

    methods: {
        SelectProduct: function(item){
            this.selected_product = item;
            this.page_product = true;
        },

        SetSortBy: function(value){
            this.sort_by = value;
        },

        SortProducts: function(){
            for(let i = 0; i < this.products.length; i++){
                for(let j = 0; j < this.products.length; j++){
                    if(this.sort_by == 0){
                        if(this.products[i].label < this.products[j].label){
                            const aux = this.products[i];
                            this.products[i] = this.products[j];
                            this.products[j] = aux;
                        }
                    }
                    else if(this.sort_by == 1){
                        if(this.products[i].price < this.products[j].price){
                            const aux = this.products[i];
                            this.products[i] = this.products[j];
                            this.products[j] = aux;
                        }
                    }
                    else if(this.sort_by == 2){
                        if(this.products[i].price > this.products[j].price){
                            const aux = this.products[i];
                            this.products[i] = this.products[j];
                            this.products[j] = aux;
                        }
                    }
                }
            }
            return this.products;
        },

        RemoveItem: function(){
            this.functions.SendToClient("RemoveItem", {
                product: this.selected_product
            });

            this.functions.OpenScreen("Transaction");
        },
        ReceiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.load_ads){
                this.products = [];

                for(let i in lua.products){
                    if ( lua.products[i] != null && lua.products[i].amount > 0 ){
                        this.products.push(lua.products[i]);
                    }
                }
                
                this.is_loading = false;
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.ReceiveLua); 

        this.is_loading = true;
        this.functions.SendToClient("LoadMyAds", {});
    },
    destroyed: function(){
        window.removeEventListener('message', this.ReceiveLua);
    }

}
</script>

<style>
    .MyAdsItemButton{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        flex: 1;
        background: #171717;
    }

    .MyAdsItemButton p{
        color: rgb(197, 74, 74);

        font-weight: bold;
        font-size: 2vh;

    }

     .ProductsItemBox:hover .MyAdsItemButton{
        background: black;
    }

    .MyAdsSelectedButtonsBox{
        display: flex;
        justify-content: center;

        margin-top: 5vh;
    }

    .MyAdsSelectedBuyButton{
        background: #d8453b;
        border-radius: 50px;

        display: flex;
        justify-content: center;
        align-items: center;

        padding: 1vh 1vh;

        flex: 1;
        max-width: 50%;
        margin-left: 2vh;
        font-size: 2vh;

        color: whitesmoke;
        text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.63);

        position: relative;
        
    }

    .MyAdsSelectedBuyButton p{
        margin-left: 2vh;
    }
</style>