<template>
    <div id="Loading" :class="{LoadingBackground: use_background}">
        <div v-if="!error">
            <div class="loader" :style="{width: `${size}vh`, height: `${size}vh`, border: `1vh solid ${color}`, borderTop: `1vh solid ${color2} !important`}"></div>

            <p class="LoadingText" :class="{Light : shared.light_mode}">{{text}}</p>
        </div>

        <div v-else>
            <div class="error">X</div>

            <p class="LoadingText">{{error_text}}</p>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        text: {},
        size: {},
        color: {
            default: "#f3f3f3",
        },
        color2: {
            default: "#3498db",
        },

        use_background: {
            default: true,
        },

        shared: {},
        functions: {},
    },

    data: function(){
        return({
            error: false,

            error_text: "MESSAGE",

            timeout: null,
        })
    },

    methods:{
        ReceiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.error){
                this.error_text = lua.error_text;
                this.error = true;

                this.timeout = setTimeout(() => {
                    this.functions.OpenScreen("Products");
                }, 3000);
            }
        }
    },

    created: function(){
        window.addEventListener('message', this.ReceiveLua); 
    },
    destroyed: function(){
        window.removeEventListener('message', this.ReceiveLua);

        if(this.timeout){
            clearTimeout(this.timeout);
        }
    }
}
</script>

<style>
    #Loading{
        width: 50vh;
        height: max-content;
        margin: auto;

        padding: 5vh;

        border-radius: 5px;
    }

    .LoadingBackground{
        background: rgba(0, 0, 0, 0.74);
    }

    .LoadingText{
        margin-top: 4vh;
        text-align: center;
        color: white;
        font-size: 2vh;
    }

    .LoadingText.Light{
        /* color: rgb(34, 34, 34); */
    }

    .error{
        text-align: center;
        font-size: 8vh;
        color: red;
        text-shadow: 0px 0px 5px black;
    }

    .loader {
        border-radius: 50%;
        animation: spin 2s linear infinite;

        margin: auto;
    }

    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }
</style>