<template>
    <div class="Products">
        <div class="LoadingBox" v-if="is_loading">
            <Loading size="10" :color2="`brown`" :text="shared.translate.TR_LOADING_NEWAD" :use_background="false" :shared="shared"/>
        </div>

        <div v-else>
        <div v-if="!page_product">
            <div class="ProductsHeaderBox">
                <div class="ProductsSearchBox">
                    <i class="fas fa-search"></i>
                    <input type="text" :placeholder="shared.translate.TR_SEARCH" v-model="search">
                </div>

                <div class="ProductsOrderByBox">
                    <p>{{shared.translate.TR_SORTBY}}:</p>
                    <p :class="{selected : sort_by == 0}" @click="SetSortBy(0)">{{shared.translate.TR_NAME}}</p>
                </div>
            </div>

            <div class="ProductsListItemsBox">
                <div class="NewAdItemBox" v-for="(item, index) in SortProducts().filter(el => el.label.toLowerCase().includes(search.toLowerCase()))" :key="`newad:${index}`">
                    <div class="OnClick" @click="SelectProduct(item)"></div>
                    <p class="NewAdItemName">{{functions.ReduceString(item.label, 13)}} x{{item.amount}}</p>
                    <div class="NewAdItemImageBox">
                        <img class="NewAdItemImage" :src="`img/${item.name}.png`" alt="">
                    </div>
                    <div class="NewAdItemButton">
                        <p>{{shared.translate.TR_ANNOUNCE}}</p>
                    </div>
                </div>
            </div>
        </div>

        <div class="ProductSelectedBox" v-else>
            <div class="ButtonBack">
                <div class="OnClick" @click="page_product = false"></div>
                <i class="fas fa-arrow-left"></i>
                <p>{{shared.translate.TR_BACK}}</p>
            </div>
            <div class="ProductSelectedImageBox">
                <p>{{selected_product.label.toUpperCase()}}</p>
                <div>
                <img :src="`img/${selected_product.name}.png`" alt="">
                </div>
            </div>

            <div class="ProductSelectedInfosBox">
                <p class="ProductSelectedName">{{selected_product.label}}</p>
                <!-- <p class="ProductSelectedOwner">{{selected_product.owner}}</p> -->
                <div class="NewAdSelectedDatasBox">
                    <div>
                        <p>{{shared.translate.TR_TOTAL_AMOUNT}}: </p>
                        <p>{{selected_product.amount}}</p>
                    </div>

                    <div v-if="selected_product.weight">
                        <p>{{shared.translate.TR_WEIGHT}}: </p>
                        <p>{{selected_product.weight}}</p>
                    </div>

                    <div>
                        <p>{{shared.translate.TR_PRICE_PRODUCT}}: </p>
                        <p>{{shared.translate.TR_SIMBOL_MONEY}} {{selected_product.price}}</p>
                    </div>

                    <div>
                        <p>{{shared.translate.TR_APPLICATION_FEE}} {{shared.application_fee*100}}%: </p>
                        <p>{{shared.translate.TR_SIMBOL_MONEY}} {{Math.floor(selected_product.price * shared.application_fee)}}</p>
                    </div>

                    <div>
                        <p>{{shared.translate.TR_FINAL_PRICE}}: </p>
                        <p>{{shared.translate.TR_SIMBOL_MONEY}} {{Math.floor(selected_product.price * shared.application_fee) + Number(selected_product.price)}}</p>
                    </div>
                </div>

                <div class="NewAdAnonymousBox" :class="{Green: anonymous}">
                    <div class="OnClick" @click="SetAnonymous(!anonymous)"></div>
                    <i class="fas fa-ghost"></i>
                    <p class="NewAdSelectedTextAnonymous">{{shared.translate.TR_ANONYMOUS}}</p>
                </div>
                <p class="NewAdSelectedTextPrice">{{shared.translate.TR_PRICE_PER_UNIT}}</p>
                <input class="NewAdSelectedPrice" type="number" v-model.number="selected_price" @input="CheckPriceValue()">
                <p class="NewAdSelectedTextPriceFee">{{shared.translate.TR_PRICE_FEE}} {{shared.translate.TR_SIMBOL_MONEY}} {{Math.floor(selected_product.price * selected_amount * shared.sell_fee)}}</p>
                <p class="NewAdSelectedTextPriceFee">{{shared.translate.TR_VALUE_RECEIVE}} {{shared.translate.TR_SIMBOL_MONEY}} {{Math.floor(selected_product.price * selected_amount)}}</p>

                <div class="NewAdSelectedButtonsBox">
                    <div class="ProductSelectedAmountBox">
                        <p @click="ChangeAmount(-1)">-</p>
                        <input type="number" v-model.number="selected_amount" @input="ChangeAmount(0, true)">
                        <p @click="ChangeAmount(+1)">+</p>
                    </div>

                    <div class="NewAdSelectedBuyButton">
                        <div class="OnClick" @click="SellProduct"></div>
                        <i class="fas fa-bullhorn"></i>
                        <p>{{shared.translate.TR_CONFIRM_ANNOUNCE}}</p>
                    </div>
                </div>
            </div>
        </div>
        </div>
        
    </div>
</template>

<script>

import Loading from '../components/Loading.vue';

export default {
    components: {
       Loading,
    },

    props:{
        shared: {},
        functions: {},
    },

    data: function(){
        return({
            products : [
                {name: "energy", label: "Energy", amount: 500, weight: 1, price: 1000, identifier: "asdad", owner: "Anonymous"},
                {name: "firstaid", label: "First Aid", amount: 500, weight: 1, price: 2000, identifier: "asdad", owner: "Anonymous"},
                {name: "weapon_pistol", label: "Pistol", amount: 500, weight: 1, price: 3000, identifier: "asdad", owner: "Anonymous"},
                {name: "weapon_heavyshotgun", label: "Heavy Shotgun", amount: 500, weight: 1, price: 5000, identifier: "asdad", owner: "Anonymous"},
                {name: "weapon_heavyshotgun", label: "Heavy Shotgun", amount: 500, weight: 1, price: 51000, identifier: "asdad", owner: "Anonymous"},
            ],

            search: "",
            sort_by: 0,

            selected_price: 0,
            selected_product: {price : 0},
            selected_amount : 1,

            anonymous: false,

            page_product: false,

            is_loading: true,
        })
    },

    methods: {
        SelectProduct: function(item){
            this.selected_product = item;
            this.selected_product.price = 0;
            this.page_product = true;
        },

        ChangeAmount: function(value, is_input){     
            if(!is_input)       
                this.selected_amount += value;

            if(this.selected_amount > this.selected_product.amount)
                this.selected_amount = this.selected_product.amount;
            else if(this.selected_amount <= 0)
                this.selected_amount = 1;
        },

        SetSortBy: function(value){
            this.sort_by = value;
        },

        SetAnonymous: function(value){
            this.anonymous = value;
        },

        SortProducts: function(){
            for(let i = 0; i < this.products.length; i++){
                for(let j = 0; j < this.products.length; j++){
                    if(this.sort_by == 0){
                        if(this.products[i].label < this.products[j].label){
                            const aux = this.products[i];
                            this.products[i] = this.products[j];
                            this.products[j] = aux;
                        }
                    }
                    else if(this.sort_by == 1){
                        if(this.products[i].price < this.products[j].price){
                            const aux = this.products[i];
                            this.products[i] = this.products[j];
                            this.products[j] = aux;
                        }
                    }
                    else if(this.sort_by == 2){
                        if(this.products[i].price > this.products[j].price){
                            const aux = this.products[i];
                            this.products[i] = this.products[j];
                            this.products[j] = aux;
                        }
                    }
                }
            }
            return this.products;
        },

        CheckPriceValue: function(){
            if(typeof(this.selected_price) != 'number')
                this.selected_price = 0;

            if(this.selected_price < 0)
                this.selected_price = 0;
            
            this.selected_product.price = this.selected_price;
        },


        SellProduct: function(){
            this.functions.SendToClient("SellProduct", {
                product: this.selected_product,
                amount: this.selected_amount,
                anonymous : this.anonymous
            });

            this.functions.OpenScreen("Transaction");
        },

        ReceiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.load_inventory){
                this.products = [];

                for(let i in lua.products){
                    if ( lua.products[i] != null && lua.products[i].amount > 0 ){
                        this.products.push(lua.products[i]);
                    }
                }
                
                this.is_loading = false;
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.ReceiveLua); 

        this.is_loading = true;
        this.functions.SendToClient("LoadInventory", {});
    },
    destroyed: function(){
        window.removeEventListener('message', this.ReceiveLua);
    }

}
</script>

<style>
    .NewAdItemBox{
        width: calc(100%/5 - 2vh + 2vh/5);
        height: 23vh;

        background: #202224;

        margin-right: 2vh;
        margin-bottom: 2vh;

        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;

        position: relative;
    }

    .NewAdItemBox:nth-child(5n){
        margin-right: 0;
    }

    .NewAdItemName{
        /* width: 100%; */
        text-align: center;

        color: #8B8B8B;
        padding: 1vh;

        font-size: 1.8vh;

        max-width: 95%;
        white-space: nowrap;
        overflow: hidden;
    }
    
    .NewAdItemImageBox{
        min-width: 14vh;
        min-height: 14vh;

        display: flex;
        justify-content: center;
        align-items: center;
    }
    .NewAdItemImage{
        max-width: 14vh;
        max-height: 14vh;
    }

    .NewAdItemButton{
        margin-top: 0.5vh;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        flex: 1;
        background: #171717;
    }

    .NewAdItemButton p{
        color: rgb(252, 252, 252);

        font-weight: bold;
        font-size: 2vh;

    }

     .NewAdItemBox:hover .NewAdItemButton{
        background: black;
    }

    .NewAdSelectedButtonsBox{
        display: flex;
        justify-content: center;

        margin-top: 5vh;
    }

    .NewAdSelectedBuyButton{
        background: #ffffff;
        border-radius: 50px;

        display: flex;
        justify-content: center;
        align-items: center;

        padding: 1vh 1vh;

        flex: 1;
        max-width: 50%;
        margin-left: 2vh;
        font-size: 2vh;

        color: rgb(0, 0, 0);
        position: relative;
        /* text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.63); */
        
    }

    .NewAdSelectedBuyButton p{
        margin-left: 2vh;
    }

    .NewAdSelectedTextPrice{
        color: #ebd31b;

        font-size: 2vh;
        font-weight: bold;
        text-shadow: 0px 0px 5px black;
        margin-top: 2vh;
    }

    .NewAdSelectedPrice{
        color: #ebd31b;

        font-size: 4vh;
        font-weight: bold;
        text-shadow: 0px 0px 5px black;
        margin-top: 1vh;

        background: none;
        border: none;
        outline: none;
    }

    .NewAdSelectedTextPriceFee{
        color: #ffffff;

        font-size: 1.5vh;
        font-weight: bold;
        text-shadow: 0px 0px 5px black;
        margin-top: 1vh;
    }

    .NewAdAnonymousBox{
        display: flex;
        flex-direction: row;
        /* justify-content: center; */
        align-items: center;
        margin-top: 2vh;

        color: rgb(238, 142, 142);
        font-size: 1.8vh;

        position: relative;
    }

    .NewAdAnonymousBox:hover{
        text-decoration: underline;
    }
    .NewAdSelectedTextAnonymous{
        margin-left: 1vh;
    }

    .Green{
        color: rgb(142, 238, 155);
    }

    .NewAdSelectedDatasBox{
        margin-top: 2vh;

        font-size: 2vh;
        color: rgb(156, 156, 156);
    }

    .NewAdSelectedDatasBox div{
        display: flex;
        justify-content: space-between;
    }

</style>