<template>
    <div>
        <div id="TransactionBox">
            <Loading :text="shared.translate.TR_TRANSACTION" :size="10" color2="orange" :use_background="false" :shared="shared" :functions="functions" />
        </div>

    </div>
</template>

<script>

import Loading from '../components/Loading.vue';

export default {
    components: {
       Loading,
    },

    props:{
        shared: {},
        functions: {},
    },

    methods:{
        ReceiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.finish_transaction){
                this.functions.OpenScreen("Products");
            }
        }
    },

    created: function(){
        window.addEventListener('message', this.ReceiveLua); 
    },
    destroyed: function(){
        window.removeEventListener('message', this.ReceiveLua);
    }

}
</script>

<style>

    #TransactionBox{
        /* background: black; */
        width: 100%;
        height: 50vh;
        display: flex;
        justify-content: center;
        align-items: center;
    }

</style>