<template>
    <div id="App">
        <div id="MarketBox" :class="{Light : shared.light_mode}" v-if="openned">
            <p class="ButtonClose" @click="Close">X</p>
            <div id="HeaderBox" :class="{Light : shared.light_mode}">
                <div id="HeaderTitleBox">
                    <p>{{shared.translate.TR_TITLE_WHITE}}</p>
                    <p>{{shared.translate.TR_TITLE_YELLOW}}</p>
                </div>

                <div id="HeaderOptions" :class="{Light : shared.light_mode}">
                    <div :class="{selected : page == 'Products'}" @click="OpenScreen('Products')">{{shared.translate.TR_PRODUCTS}}</div>
                    <div :class="{selected : page == 'MyAds'}" @click="OpenScreen('MyAds')">{{shared.translate.TR_MYADS}}</div>
                    <div :class="{selected : page == 'NewAd'}" @click="OpenScreen('NewAd')">{{shared.translate.TR_NEWAD}}</div>
                </div>

                <div id="HeaderSolds" :class="{Light : shared.light_mode}" @click="OpenScreen('MySolds')">{{shared.translate.TR_SOLD}} {{shared.translate.TR_SIMBOL_MONEY}} {{total_solds}}</div>
            </div>

            <Products v-if="page == 'Products'" :shared="shared" :functions="functions" />
            <MyAds v-else-if="page == 'MyAds'" :shared="shared" :functions="functions"/>
            <MySolds v-else-if="page == 'MySolds'" :shared="shared" :functions="functions"/>
            <NewAd v-else-if="page == 'NewAd'" :shared="shared" :functions="functions"/>
            <Transaction v-else-if="page == 'Transaction'" :shared="shared" :functions="functions"/>
        </div>

        <!-- <div id="Notify" v-if="notify">
            {{messageNotify}}
        </div> -->
    </div>
</template>

<script>

import axios from 'axios';
import Products from './screens/Products.vue';
import MyAds from './screens/MyAds.vue';
import MySolds from './screens/MySolds.vue';
import NewAd from './screens/NewAd.vue';
import Transaction from './screens/Transaction.vue';

export default {
    components: {
        Products,
        MyAds,
        MySolds,
        NewAd,
        Transaction,
    },

    data: function(){
        return({
            page : "",

            total_solds: 0,

            shared : {
                application_fee: 0.5,
                sell_fee: 0.2,

                light_mode: false,

                translate:{
                    TR_TITLE_WHITE: "MARKET",
                    TR_TITLE_YELLOW: "PLACE",
                    TR_PRODUCTS: "PRODUCTS",
                    TR_MYADS: "MY ADS",
                    TR_NEWAD: "NEW AD",
                    TR_SOLD: "SOLD",
                    TR_SORTBY: "SORT BY",
                    TR_SEARCH: "Search for a product",
                    TR_NAME: "Name",
                    TR_LOWEST: "Lowest Price",
                    TR_BIGGEST: "Biggest Price",
                    TR_WHITE_BUY: "BUY",
                    TR_YELLOW_BUY: "NOW",
                    TR_BY: "By",
                    TR_SIMBOL_MONEY: "$",
                    TR_BACK: "Back",
                    TR_TOTAL_AMOUNT: "Total Amount",
                    TR_WEIGHT: "Weight",
                    TR_PRICE_PRODUCT: "Price of Product",
                    TR_APPLICATION_FEE: "Applcation Fee",
                    TR_FINAL_PRICE: "Final Price Per Unit",
                    TR_CONFIRM_BUY: "Confirm Buy",
                    TR_REMOVE: "REMOVE",
                    TR_CONFIRM_REMOVE: "Confirm Remove",
                    TR_COLLECT: "RECEIVE",
                    TR_ANNOUNCE: "ANNOUNCE",
                    TR_CONFIRM_ANNOUNCE: "Confirm Announce",
                    TR_MESSAGE_REMOVE: "You will not pay anything when you remove this item.",
                    TR_PRICE_PER_UNIT: "What will the price per unit be?",
                    TR_ANONYMOUS: "Anonymous",
                    TR_PRICE_FEE: "To advertise this item, you will pay a fee of",
                    TR_VALUE_RECEIVE: "When you sell the advertised amount, you will receive",
                    TR_LOADING_PRODUCTS: "Loading Products...",
                    TR_LOADING_MYADS: "Loading Your Ads...",
                    TR_LOADING_NEWAD: "Loading Your Inventory...",
                    TR_LOADING_SOLD: "Loading Your Solds...",
                    TR_TRANSACTION: "Performing Transaction...",
                },
            },

            functions : {

            },

            name_resource: "Sam_market",
            openned: false,
        })
    },

    methods: {
        OpenScreen: function(name){
            this.page = name;
        },

        ReduceString: function(text, max_characters){
            let text_return = text;
            if(text_return.length > max_characters){
                text_return = text_return.substring(0, max_characters);
            }

            return text_return;
        },

        SetTotalSolds: function(value){
            this.total_solds = value;
        },

        SetFunctions: function(){
            this.functions = {
                ReduceString: this.ReduceString,
                SetTotalSolds: this.SetTotalSolds,
                OpenScreen: this.OpenScreen,
                SendToClient: this.SendToClient,
                Close: this.Close,
            }
        },

        ReceiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.config){
                this.shared.translate = lua.translate;
                this.shared.application_fee = lua.application_fee;
                this.shared.sell_fee = lua.sell_fee;
                this.name_resource = lua.name_resource;  
            }
            else if(lua.open){
                this.page = "Products";
                this.openned = true;
            }
        },

        SendToClient: function(to, data){
            axios.post(`https://${this.name_resource}/${to}`, data)
            .catch(msg => console.log(`An error occurred in: ${to} , ${msg}`));
        },

        Close: function(){     
            this.SendToClient('close', {});
            this.openned = false;
            this.page = "";
        },

        KeyPress: function(event){
            const key = event.key;
            
            if(key == "Escape"){
                this.Close();
            }
        },
    },

    created: function(){
        this.SetFunctions();
        window.addEventListener('message', this.ReceiveLua);  
        window.addEventListener('keydown', this.KeyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.ReceiveLua);
        window.removeEventListener('keydown', this.KeyPress);
    }

}
</script>

<style>
    /* #07051A */
    /* #FCE400 */
    /* #0A0727 */

    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        list-style: none;
        font-family: 'Roboto', sans-serif;
    }

    *:not(input or select){
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
    }

    .GrayScale{
        filter: grayscale(1);
        /* blur(1vh) */
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .OnClick{
        width: 100%;
        height: 100%;
        /* background: black; */
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
    }

    #Notify{
        position: absolute;
        bottom: 20%;
        left: 50%;
        transform: translate(-50%, 20%);

        /* background: rgba(0, 0, 0, 0.911); */
        font-size: 2vh;
        width: max-content;
        padding: 1vh 2vh;
        border-radius: 3px;
        color: white;
        text-shadow: 0px 0px 5px black;
    }

    #MarketBox{
        width: 120vh;
        height: 80vh;

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        background: #1A1C1E;

        padding: 2vh 5vh;
    }

    #MarketBox.Light{
        background: #E3E6EB;
    }

    #HeaderBox{
        width: 100%;
        display: flex;
        justify-content: space-between;

        background: #202224;
    }

    #HeaderBox.Light{
        background: #526174;
    }

    #HeaderTitleBox{
        display: flex;

        font-size: 3vh;
        font-weight: bold;

        padding: 1.5vh;
    }

    #HeaderTitleBox p:first-child{
        color: white;
    }
    #HeaderTitleBox p:last-child{
        color: #EBD31B;
        /* margin-left: 1vh; */
    }

    #HeaderBox.Light #HeaderTitleBox p:last-child{
        color: #43e643;
        color: #4ee28c;
    }

    #HeaderOptions{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 2vh;
        /* color: white; */
        /* background: black; */

    }

    #HeaderOptions div{
        margin: 0 2vh;
        /* border-bottom: 2px solid #EBD31B; */
        height: 100%;

        display: flex;
        justify-content: center;
        align-items: center;

        color: rgb(167, 167, 167);
    }

    #HeaderOptions div.selected{
        color: white;
        border-bottom: 2px solid #EBD31B;
    }

    #HeaderOptions.Light div{
        color: rgb(211, 211, 211);
        font-weight: bold;
    }

    #HeaderOptions.Light div.selected{
        border-bottom: 2px solid #4ee28c;
    }

    #HeaderSolds{
        align-self: center;
        height: 100%;

        /* display: flex;
        justify-content: center;
        align-items: center; */

        font-size: 1.6vh;
        font-weight: bold;
        background: #1C1E20;
        color: #438D57;
        padding: 1vh;
        border-radius: 3px;
        margin-right: 1.5vh;
    }

    #HeaderSolds.Light{
        background: rgba(0, 0, 0, 0.1);
        color: #57b871;
    }

    .ButtonClose{
        position: absolute;
        right: 1%;
        top: 1%;

        font-size: 1.5vh;
        color: rgb(194, 194, 194);
    }
</style>